	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/1/the-truth-about-aaron-swartzs-crime/index.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>Aaron Swartz was not the super hacker breathlessly described in the Government’s indictment and forensic reports, and his actions did not pose a real danger to JSTOR, MIT or the public. He was an intelligent young man who found a loophole that would allow him to download a lot of documents quickly. This loophole was created intentionally by MIT and JSTOR, and was codified contractually in the piles of paperwork turned over during discovery.</p>\n</blockquote>","frontmatter":{"title":"The truth about Aaron Swartz's \"crime\"","date":"January 14, 2013","passthroughUrl":"http://unhandled.com/2013/01/12/the-truth-about-aaron-swartzs-crime/"}}},"pathContext":{"slug":"/blog/2013/1/the-truth-about-aaron-swartzs-crime/","previous":{"fields":{"slug":"/blog/2013/1/boeing-has-an-aeroplane-problem/"},"frontmatter":{"title":"Boeing has an aeroplane problem","passthroughUrl":"http://www.forbes.com/sites/jonathansalembaskin/2013/01/10/boeing-has-an-airplane-problem-not-a-pr-problem/","type":null}},"next":{"fields":{"slug":"/blog/2013/1/a-history-of-apple-products/"},"frontmatter":{"title":"A history of Apple products","passthroughUrl":"http://popchartlab.com/collections/prints/products/the-insanely-great-history-of-apple","type":null}}}}

